import React, { Component } from 'react'
import logo from './logo.svg';

export default class App extends Component {
constructor(props){
  super(props);
  this.state = {}
}

    componentDidMount(){
        const url = "https://jogavidia.github.io/ctperu-json/ctperu.json";

        fetch(url)
            .then(result => result.json())
            .then(result => {
              this.setState({
                data: result
              })
            });
    }

  render() {
    if (!this.state.data) return <p>Loading...</p>
    return(
      <div className="wrap">
        <div className="header">

        <img className="logo" src={logo} alt="Logo" />
       
          {/*<h1>{this.state.data.blog}</h1> */}
          
          <p>{this.state.data.about}</p>
        </div>
        <div className="container">
        {
          this.state.data.cities.map((postDetail) =>{
            return (
              <div key={postDetail.id.toString()} className="info">
              <div className="callout">
                <h2>{postDetail.cityName}</h2>
              </div>
              <div className="callout">
                <p> <span>{postDetail.place}</span>{postDetail.description}</p>
              </div>  
                <div className="callout">
                  <div className="embajador-box">
                    <p>{postDetail.gender}<a href={"https://www.instagram.com/"+postDetail.embajador}>@{postDetail.embajador}</a></p>
                  </div>
                </div>
              </div>
            )
          })
        }
        </div>
      </div>
    )
  }
}
